import NavBar from "@/components/NavBar"
import {Button, Form, Input, message} from "antd"
import React, {FunctionComponent, PropsWithChildren} from "react"
import {RegisterPayload} from "@/typings/user"
import {Link, RouteComponentProps} from "react-router-dom"
import actions from '@/store/actions/user'
import {connect} from "react-redux"
import {ProfileState, RootState} from '@/store/reducers'
import styles from './index.module.less'
import {UserOutlined, LockOutlined, MailOutlined} from "@ant-design/icons"

type DispatchProps = typeof actions
type StateProps = ReturnType<typeof mapStateToProps>
type Props = PropsWithChildren<RouteComponentProps> & StateProps & DispatchProps
const Register: FunctionComponent = (props: Props) => {
  function onFinish(data: RegisterPayload) {
    console.log('form data', data)
    props.register(data)
  }

  function onFinishFailed(error: any) {
    message.error('')
  }

  return (
    <>
      <NavBar></NavBar>
      <Form
        className={styles.form}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
      >
        <Form.Item
          label=""
          name="username"
          rules={[{required: true, message: ''}]}
        >
          <Input
            prefix={<UserOutlined/>}
            placeholder=""
          />
        </Form.Item>
        <Form.Item
          label=""
          name="password"
          rules={[{required: true, message: ''}]}
        >
          <Input.Password
            prefix={<LockOutlined/>}
            placeholder=""
          />
        </Form.Item>
        <Form.Item
          label=""
          name="confirmPassword"
          rules={[{required: true, message: ''}]}
        >
          <Input.Password
            prefix={<LockOutlined/>}
            placeholder=""
          />
        </Form.Item>
        <Form.Item
          label=""
          name="email"
          rules={[{type: 'email'}]}
        >
          <Input
            prefix={<MailOutlined/>}
            placeholder=""
          />
        </Form.Item>
        <Form.Item>
          <Button
            type="primary"
            htmlType="submit"
          >
            
          </Button>
          &nbsp;&nbsp;
          <Link to="/login"></Link>
        </Form.Item>
      </Form>
    </>
  )
}
const mapStateToProps = (state: RootState): ProfileState => state.profile
export default connect(mapStateToProps, actions)(Register)